<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<!-- eslint-disable max-len -->
<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img
        :src="item.product.image"
        width="120"
        height="120"
        :alt="item.product.title"
      />
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <span class="product__code"> Артикул: {{ item.product.id }} </span>

    <ProductCount :amount.sync="amount"></ProductCount>

    <b class="product__price">
      {{ (item.amount * item.product.price) | numberFormat }} &#8381;
    </b>

    <button
      class="product__del button-del"
      type="button"
      aria-label="Удалить товар из корзины"
      @click.prevent="deleteCartProduct(item.product.id)"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import ProductCount from '@/components/ProductCount.vue';
import { mapActions } from 'vuex';

export default {
  filters: { numberFormat },
  props: ['item'],
  components: { ProductCount },
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.updateCartProduct({ productId: this.item.productId, amount: value });
      },
    },
  },
  methods: {
    ...mapActions(['deleteCartProduct', 'updateCartProduct']),
  },
};
</script>
